	<div>
		  <el-table
		    ref="filterTable"
		    :data="tableData"
		    style="width: 100% ;font-size: 24px;">
		    <el-table-column
		      prop="createTime"
		      label="日期"
		      sortable
		      width="180"
		      column-key="createTime"
			  :formatter="dateFormatter"
		    >
		    </el-table-column>
		    <el-table-column
		      prop="footName"
		      label="菜名"
		      width="180">
		    </el-table-column>
			<el-table-column
			  prop="num"
			  label="数量"
			  width="180">
			  </el-table-column>
		    <el-table-column
		      prop="tableId"
		      label="桌号"
			  width="180"
		      :formatter="formatter">
		    </el-table-column>
			<el-table-column
				prop="remark"
				label="备注"
				width="180">
			</el-table-column>
		    <el-table-column
		      prop="name"
		      label="标签"
		      width="180">
		      <template slot-scope="scope">
		        <el-tag
		          :type="scope.row.name === '烧烤' ? 'danger' : 'success'"
		          disable-transitions>{{scope.row.name}}</el-tag>
		      </template>
		    </el-table-column>
			<el-table-column
			 prop ="createTime"
			 label="用时"
			 width="180" 
			 column-key="createTime"
			 :formatter="userTimeFormatter">
			</el-table-column>
		  </el-table>
		  
	</div>
</template>

<script>
	import {listOrderSkuNeedCooking,findMainOrderInfo} from '../../api/index.js'
	export default{
		data(){
			return {
				headers: {
				        'token':sessionStorage.token  //从cookie里获取token，并赋值  Authorization ，而不是token
				      },
				tableData: [{
					  "tableId": 13,
					  "createTime": "2019-11-02 19:49:46",
					  "footName": "韭菜",
					  "num": 2,
					  "remark": "",
					  "name": "烧烤",
					  "type": 3,
					  "userTime":0
					}],
				time:'',	
				
			}
		},
		methods:{
			  formatter(row, column) {
				return row.tableId;
			  },
			  dateFormatter(row, column){
			    var t=new Date(row.createTime);//row 表示一行数据, updateTime 表示要格式化的字段名称
				return t.getHours()+":"+t.getMinutes()
			  },
			  userTimeFormatter(row, column){
					var t = new Date(row.createTime)
					var nT = new Date()
					var h = nT.getHours() - t.getHours()
					var m = (nT.getMinutes()-t.getMinutes())
					if(m<0){
						h--
						m+=60
					}
					var s = (nT.getSeconds()-t.getSeconds())
					if(s<0){
						m--
						s+=60
					}
					return h+":"+ m+":"+s
			  },
			  timeInterVal(){
				  this.time = window.setInterval(this.timeDo,60000)
			  },
			  timeDo(){
				this.getCookingInfo()  
			  },
			  getCookingInfo(){
				  findMainOrderInfo().then(r=>{  
					if(r.data.code==201){
						this.$router.push('/');
					}  
					this.tableData = r.data
				  })
			  },
		},
		created() {
			this.getCookingInfo()
			this.timeInterVal()
		},
		beforeDestroy() {
			window.clearInterval(this.time)
		}
	}
</script>

